<template lang="pug">
  .w-slider(:class="wrapperClasses")
    .w-slider__label.w-slider__label--left(v-if="$slots['label-left']")
      slot(name="label-left")
    .w-slider__label.w-slider__label--left(v-else-if="labelLeft" v-html="labelLeft")
    .w-slider__track(
      ref="track"
      @mousedown="onTrackMouseDown"
      :class="trackClasses"
      role="slider"
      aria-label="Slider"
      :aria-valuemin="minVal"
      :aria-valuemax="maxVal"
      :aria-valuenow="rangeValueScaled"
      :aria-readonly="readonly"
      aria-orientation="horizontal")
      .w-slider__range(:class="rangeClasses" :style="rangeStyles")
      .w-slider__thumb(:style="thumbStyles")
        button.w-slider__thumb-button(
          ref="thumb"
          :id="`button-${_uid}`" :class="[color]"
          :name="inputName"
          :value="rangeValueScaled"
          :disabled="disabled"
          :readonly="readonly"
          @keydown.left="onKeyDown($event, -1)"
          @keydown.right="onKeyDown($event, 1)")
        label.w-slider__thumb-label(
          v-if="thumbLabel"
          :for="`button-${_uid}`"
          :class="thumbClasses")
          div(v-if="thumbLabel === 'droplet'")
            slot(name="label" :value="rangeValueScaled") {{ ~~rangeValueScaled }}
          slot(v-else name="label" :value="rangeValueScaled") {{ ~~rangeValueScaled }}
    .w-slider__label.w-slider__label--right(v-if="$slots['label-right']")
      slot(name="label-right")
    .w-slider__label.w-slider__label--right(v-else-if="labelRight" v-html="labelRight")
</template>

<script>
export default {
  name: 'w-slider',
  props: {
    value: { type: Number, default: 0 },
    color: { type: String, default: 'primary' },
    name: { type: String }, // When sending data through form.
    bgColor: { type: String },
    thumbLabel: { type: [Boolean, String] }, // One of true, false, 'droplet'
    thumbLabelClass: { type: String },
    trackClass: { type: String },
    rangeClass: { type: String },
    min: { type: [Number, String], default: 0 },
    max: { type: [Number, String], default: 100 },
    step: { type: [Number, String] },
    disabled: { type: Boolean },
    readonly: { type: Boolean },
    labelLeft: { type: String },
    labelRight: { type: String }
  },

  data: () => ({
    track: {
      el: null,
      left: 0,
      width: 0
    },
    dragging: false,
    rangeValuePercent: 0,
    rangeValueScaled: 0
  }),

  computed: {
    inputName () {
      return this.name || `w-slider-${this._uid}`
    },
    minVal () {
      return parseFloat(this.min)
    },
    maxVal () {
      return parseFloat(this.max)
    },
    // Lighten the maths while dragging by caching some of the maths - it's already that!
    scaledRange () {
      return this.maxVal - this.minVal
    },
    rangeStyles () {
      return {
        width: `${this.rangeValuePercent}%`
      }
    },
    thumbStyles () {
      return {
        left: `${this.rangeValuePercent}%`
      }
    },
    rangeClasses () {
      return {
        [`${this.color}--bg`]: this.color,
        [this.rangeClass]: this.rangeClass || null
      }
    },
    trackClasses () {
      return {
        [`${this.bgColor}--bg`]: this.bgColor,
        [this.trackClass]: this.trackClass || null
      }
    },
    thumbClasses () {
      return {
        [this.thumbLabelClass]: this.thumbLabelClass || null,
        'w-slider__thumb-label--droplet': this.thumbLabel === 'droplet'
      }
    },
    wrapperClasses () {
      return {
        'w-slider--dragging': this.dragging,
        'w-slider--disabled': this.disabled,
        'w-slider--readonly': this.readonly
      }
    }
  },

  methods: {
    scaledToPercent (value) {
      // percentage = (value - min) / (max - min)
      return Math.max(0, Math.min((value - this.minVal) / this.scaledRange * 100, 100))
    },

    percentToScaled (value) {
      return ((value / 100) * this.scaledRange) + this.minVal
    },

    onTrackMouseDown (e) {
      if (this.disabled || this.readonly) return

      const { left, width } = this.track.el.getBoundingClientRect()
      this.track.width = width
      this.track.left = left
      this.dragging = true

      this.updateRange(e.clientX)

      document.addEventListener('mousemove', this.onDrag)
      document.addEventListener('mouseup', this.onMouseUp, { once: true })
    },

    onDrag (e) {
      this.updateRange(e.clientX)
    },

    onMouseUp () {
      this.dragging = false
      document.removeEventListener('mousemove', this.onDrag)
      this.$refs.thumb.focus()
    },

    onKeyDown (e, direction) {
      if (this.disabled || this.readonly) return

      this.rangeValuePercent += direction * (e.shiftKey ? 5 : 1)
      this.rangeValuePercent = Math.max(0, Math.min(this.rangeValuePercent, 100))
      this.rangeValueScaled = this.percentToScaled(this.rangeValuePercent)
      this.$emit('input', this.rangeValueScaled)
    },

    updateRange (cursorPositionX) {
      this.rangeValuePercent = Math.max(0, Math.min(((cursorPositionX - this.track.left) / this.track.width) * 100, 100))
      this.rangeValueScaled = this.percentToScaled(this.rangeValuePercent)
      this.$emit('input', this.rangeValueScaled)
    }
  },

  beforeMount () {
    this.$nextTick(() => {
      this.track.el = this.$refs.track
      this.rangeValueScaled = this.value
      this.rangeValuePercent = this.scaledToPercent(this.value)
    })
  },

  watch: {
    value (value) {
      this.rangeValueScaled = value
      this.rangeValuePercent = this.scaledToPercent(value)
    }
  }
}
</script>

<style lang="scss">
.w-slider {
  position: relative;
  display: flex;
  align-items: center;
  user-select: none;
  min-width: 20px;

  &__track, &__range {
    cursor: pointer;

    .w-slider--disabled &, .w-slider--readonly & {cursor: auto;}
  }

  // Slider label, left & right.
  // ------------------------------------------------------
  &__label--left {margin-right: 3 * $base-increment;}
  &__label--right {margin-left: 3 * $base-increment;}

  // Track.
  // ------------------------------------------------------
  &__track {
    position: relative;
    flex-grow: 1;
    height: $slider-height;
    background-color: $slider-track-color;
    border-radius: $border-radius;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      // For fat fingers.
      top: -8px;
      bottom: -8px;
    }
  }

  // Range.
  // ------------------------------------------------------
  &__range {
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    z-index: 1;
    transition: $transition-duration;
    border-radius: inherit;

    .w-slider--dragging & {transition: none;}
    .w-slider--disabled & {opacity: 0.35;}
  }

  // Thumb.
  // ------------------------------------------------------
  &__thumb {
    position: absolute;
    width: 3 * $base-increment;
    height: 3 * $base-increment;
    left: 100%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    transition: $transition-duration;

    .w-slider--dragging & {transition: none;}
  }

  &__thumb-button {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 5em;
    cursor: pointer;
    background-color: #fff;

    .w-slider--disabled &, .w-slider--readonly & {cursor: auto;}

    &:before, &:after {
      content: '';
      position: absolute;
    }
    &:before {
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0.5;
      border-radius: inherit;
      border: 1px solid currentColor;
      @include default-transition;
    }
    &:hover:before, &:focus:before {opacity: 0.7;}
    &:active:before, .w-slider--dragging &:before {
      opacity: 1;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
      transition-duration: $fast-transition-duration;
    }
    .w-slider--disabled &:before,
    .w-slider--readonly &:before {box-shadow: none;opacity: 0.4;}

    // For fat fingers.
    &:after {
      left: -5px;
      right: -5px;
      top: -5px;
      bottom: -5px;
    }
  }

  // Thumb label.
  // ------------------------------------------------------
  &__thumb-label {
    position: absolute;
    left: 50%;
    bottom: 100%;
    margin-bottom: round(3 * $base-increment);
    transform: translateX(-50%);
    padding: round(0.75 * $base-increment) (2 * $base-increment);
    background-color: #fff;
    border-radius: $border-radius;
    border: $border;
    font-size: 0.85em;
    color:rgba(0, 0, 0, 0.7);

    &:before, &:after {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 0;
      border: solid transparent;
    }

    &:before {border-width: 7px;border-top-color: inherit;}
    &:after {border-width: 6px;border-top-color: #fff;}

    &--droplet {
      transform: translateX(-50%) rotate(-45deg);
      border-radius: 5em 5em 5em 0;
      width: 2.8em;
      height: 2.8em;

      & > div {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        transform: rotate(45deg);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1em;
      }

      &:before, &:after {display: none;}
    }
  }
}
</style>
